html {
  border-top: 5px solid #000;
  font-size: 115%;
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 100%;
  }
}

body {
  font-family: "Newsreader", serif;
  font-weight: 300;
  font-style: normal;
  line-height: 1.66;
  color: #666;
  padding-left: 15px;
  padding-right: 15px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  color: #333;
}

h1 {
  font-size: 2rem;
  line-height: 1.1;
}

@media only screen and (max-width: 600px) {
  h1 {
    font-size: 1.8rem;
    line-height: 1.2
  }
}

h2 {
  font-size: 1.6rem;
}

@media only screen and (max-width: 600px) {
  h2 {
    font-size: 1.5rem;
  }
}

h3 {
  font-size: 1.3rem
}

@media only screen and (max-width: 600px) {
  h3 {
    font-size: 1.2em
  }
}

h4 {
  font-size: 1.1rem
}

h5 {
  font-size: 1rem
}

h6 {
  font-size: 0.88rem
}

.inner {
  max-width: 800px;
}

header {
  border-bottom: 1px solid #ebebeb;
}

.site-title {
  font-size: calc(1vw + 2rem);
  font-weight: 600;
  font-style: normal;
  text-decoration: none;
}

.post {
  border-bottom: 1px solid #ebebeb;
}

time {
  color: #999;
  font-size: 14px;
}

.read-more {
  color: #333;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}

footer {
  border-top: 1px solid #ebebeb;
  font-size: 14px;
}